import { Header } from "../components/Header";
import styles from "./Lk.module.css";
import {
  useUserEmail,
  useSetStatus,
  useClearCart,
  usePurchasesInfo,
  usePurchaseList,
} from "../store/store";
import { useAuth } from "../hooks/useAuth";
import { Footer } from "../components/Footer";
import { Cart } from "../components/Cart";
import { useNavigate } from "react-router-dom";
import { HashLink } from "react-router-hash-link";
import { Menu } from "../components/Menu";

export default function Lk() {
  const email = useUserEmail();
  const navigate = useNavigate();
  const setStatus = useSetStatus();
  const clearCart = useClearCart();
  const purchaseInfo = usePurchasesInfo();
  const purchasesList = usePurchaseList();
  const { extendCouse } = useAuth();

  const extendCouseHandler = async (id) => {
    const res = await extendCouse(id);
    window.location.assign(res);
  };

  const pluralRu = (n, one, two, five) => {
    const mod10 = n % 10;
    const mod100 = n % 100;
    if (mod10 === 1 && mod100 !== 11) return one;
    if (mod10 >= 2 && mod10 <= 4 && (mod100 < 10 || mod100 >= 20)) return two;
    return five;
  };

  const getLeftText = (id) => {
    const item = purchasesList.find((p) => p.ProductID === id);
    if (!item) return null;

    const days = Number(item.DifferenceTime.Days ?? 0);
    const hours = Number(item.DifferenceTime.Hours ?? 0);

    if (days > 0) {
      const d = days + (hours > 0 ? 1 : 0);
      return `${d} ${pluralRu(d, "день", "дня", "дней")}`;
    }

    const h = Math.max(1, hours);
    return `${h} ${pluralRu(h, "час", "часа", "часов")}`;
  };

  return (
    <div className={styles["lk__wrapper"]}>
      <Header userButtonVariant="modal" />
      <div className={styles["lk__inside-wrapper"]}>
        <div className={styles["lk__utils"]}>
          <div>
            <h2 className={styles["lk__title"]}>Личный кабинет</h2>
            <p className={styles["lk__paragraph"]}>Почта: {email}</p>
          </div>
          <button
            onClick={() => {
              setStatus("guest");
              localStorage.removeItem("token");
              navigate("/");
              window.scrollTo(0, 0);
              clearCart();
            }}
            className={styles["exit_button"]}
          >
            Выйти из аккаунта
          </button>
        </div>
        <p className={styles["lk__my-trainig"]}>Мои тренировки</p>
        {Object.keys(purchaseInfo).length > 0 ? (
          <div className={styles["lk__trainings"]}>
            {purchaseInfo[1] != null && (
              <div className={styles["lk__trainings-element"]}>
                <img
                  src="/images/training_1.webp"
                  alt="Тренировка №1"
                  className={styles["training_img"]}
                />
                <div className={styles["lk__trainings-element-wrapper"]}>
                  <div
                    className={styles["lk__trainings-element-second-wrapper"]}
                  >
                    <p>
                      Тренировки дома - <br />
                      уровень “новичок”
                    </p>
                    <div className={styles["lk__trainings-element-time"]}>
                      <p>До конца {getLeftText(1)}</p>
                      <button
                        className={styles["lk_trainings-element-extend-btn"]}
                        onClick={() => extendCouseHandler(1)}
                      >
                        продлить курс
                      </button>
                    </div>
                  </div>
                  <button
                    className={styles["lk__trainings-element-proceed-btn"]}
                  >
                    Перейти к тренировке
                  </button>
                </div>
              </div>
            )}
            {purchaseInfo[2] != null && (
              <div className={styles["lk__trainings-element"]}>
                <img
                  src="/images/training_2.webp"
                  alt="Тренировка №2"
                  className={styles["training_img"]}
                />
                <div className={styles["lk__trainings-element-wrapper"]}>
                  <div
                    className={styles["lk__trainings-element-second-wrapper"]}
                  >
                    <p>
                      Тренировки для зала <br />
                      full body
                    </p>
                    <div className={styles["lk__trainings-element-time"]}>
                      <p>До конца {getLeftText(2)}</p>
                      <button
                        className={styles["lk_trainings-element-extend-btn"]}
                        onClick={() => extendCouseHandler(2)}
                      >
                        продлить курс
                      </button>
                    </div>
                  </div>
                  <button
                    className={styles["lk__trainings-element-proceed-btn"]}
                  >
                    Перейти к тренировке
                  </button>
                </div>
              </div>
            )}
            {purchaseInfo[3] != null && (
              <div className={styles["lk__trainings-element"]}>
                <img
                  src="/images/training_3.webp"
                  alt="Тренировка №3"
                  className={styles["training_img"]}
                />
                <div className={styles["lk__trainings-element-wrapper"]}>
                  <div
                    className={styles["lk__trainings-element-second-wrapper"]}
                  >
                    <p>
                      Тренировки дома - <br />
                      уровень “продвинутый”
                    </p>
                    <div className={styles["lk__trainings-element-time"]}>
                      <p>До конца {getLeftText(3)}</p>
                      <button
                        className={styles["lk_trainings-element-extend-btn"]}
                        onClick={() => extendCouseHandler(3)}
                      >
                        продлить курс
                      </button>
                    </div>
                  </div>
                  <button
                    className={styles["lk__trainings-element-proceed-btn"]}
                  >
                    Перейти к тренировке
                  </button>
                </div>
              </div>
            )}
            {purchaseInfo[4] != null && (
              <div className={styles["lk__trainings-element"]}>
                <img
                  src="/images/training_4.webp"
                  alt="Тренировка №4"
                  className={styles["training_img"]}
                />
                <div className={styles["lk__trainings-element-wrapper"]}>
                  <div
                    className={styles["lk__trainings-element-second-wrapper"]}
                  >
                    <p>
                      Тренировки дома - <br />
                      уровень “новичок”
                    </p>
                    <div className={styles["lk__trainings-element-time"]}>
                      <p>До конца {getLeftText(4)}</p>
                      <button
                        className={styles["lk_trainings-element-extend-btn"]}
                        onClick={() => extendCouseHandler(4)}
                      >
                        продлить курс
                      </button>
                    </div>
                  </div>
                  <button
                    className={styles["lk__trainings-element-proceed-btn"]}
                  >
                    Перейти к тренировке
                  </button>
                </div>
              </div>
            )}
          </div>
        ) : (
          <div className={styles["empty_cart"]}>
            <h2>Упс...у вас нет тренировок...</h2>
            <p>
              Посмотрите, какие классные тренировки доступны. Выбирайте и бегите
              тренироваться!
            </p>
            <button onClick={() => navigate("/#training")}>
              <HashLink smooth to="/#training">
                В КАТАЛОГ
              </HashLink>
              <svg
                width="7"
                height="7"
                viewBox="0 0 7 7"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  d="M6.51037 0.500372C6.51037 0.22423 6.28651 0.000372279 6.01037 0.000372153L1.51037 0.000372553C1.23423 0.000372385 1.01037 0.22423 1.01037 0.500372C1.01037 0.776515 1.23423 1.00037 1.51037 1.00037L5.51037 1.00037L5.51037 5.00037C5.51037 5.27651 5.73423 5.50037 6.01037 5.50037C6.28651 5.50037 6.51037 5.27651 6.51037 5.00037L6.51037 0.500372ZM0.353516 6.15723L0.707069 6.51078L6.36392 0.853926L6.01037 0.500372L5.65682 0.146819L-3.77595e-05 5.80367L0.353516 6.15723Z"
                  fill="#FBE0F3"
                />
              </svg>
            </button>
          </div>
        )}
      </div>
      <Footer />
      <Cart />
      <Menu />
    </div>
  );
}
